	module.exports = {"data":{"markdownRemark":{"html":"<p>I wanted to share this <a href=\"https://www.reddit.com/r/bestof/comments/7q7v91/udeggit_s_linguistic_analysis_of_donald_trumps/\">Reddit r/bestof post</a> of a user’s insightful analysis of Donald Trump’s speech patterns, and this <a href=\"https://www.nytimes.com/2017/09/08/style/donald-trump-yuge-sad-failing.html?mcubz=1\">New York Times piece</a> by Teddy Wane which further explores his speaking style and its collective effect on the nation.</p>\n<p>Language is a beautiful thing. That can be difficult to remember when we are so regularly exposed to the “verbal floatsam” that so consistently flows from Trump’s sweaty lips and Twitter account. But we can choose not to communicate using these elementary, repetitive sounds bites and instead choose to use language on our own terms, with our own terms.</p>","timeToRead":1,"tableOfContents":"","fields":{"tagSlugs":["/tags/trump/","/tags/linguistic-analysis/","/tags/reddit/"]},"frontmatter":{"title":"Linguistic analysis of Donald Trump's speech patterns","tags":["Trump","linguistic analysis","Reddit"],"date":"January 15, 2018","author":{"id":"Evan Nichols","bio":"Software Engineer and outdoor enthusiast. EngRes @ Google NYC.","avatar":{"children":[{"responsiveResolution":{"src":"/static/4c8a15d8009c77284919b7aa2785b3cc-88e85.jpg","srcSet":"/static/4c8a15d8009c77284919b7aa2785b3cc-88e85.jpg 1x,\n/static/4c8a15d8009c77284919b7aa2785b3cc-db17a.jpg 1.5x,\n/static/4c8a15d8009c77284919b7aa2785b3cc-599b9.jpg 2x,\n/static/4c8a15d8009c77284919b7aa2785b3cc-3e248.jpg 3x"}}]}}}}},"pathContext":{"slug":"/trump-linguistic-analysis/"}}

